<template>
  <div v-if="data">
    <h2> </h2>
    <table class="table">
      <tbody>
        <tr>
          <th></th>
          <td>
            <input v-model="data.name" type="text">
          </td>
        </tr>
        <tr>
          <th></th>
          <td>
            <input v-model="data.phone" type="text">
          </td>
        </tr>
        <tr>
          <th></th>
          <td>
            <input v-model="data.address" type="text">
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: null
    };
  },
  created() {
    this.data = JSON.parse(sessionStorage.getItem("user"));
    this.$emit("receiver", this.data);
  }
};
</script>

<style>
</style>



