<template>
  <div>
    <form @submit.prevent="doPost" method="POST">
      <!--  -->
      <div class="row">
        <div class="col-5"></div>
        <div class="col-2">
          <logo></logo>
        </div>
        <div class="col-5"></div>
      </div>

      <!--  -->
      <div class="row">
        <div class="col-3"></div>
        <div class="col-6">
          <div class="input-group">
            <div class="input-group-prepend">
              <label class="btn btn-outline-secondary" id="button-addon1" for="mailbox">
                <i class="far fa-envelope"></i>
              </label>
            </div>
            <input
              ref="email"
              id="mailbox"
              type="email"
              name="email"
              class="form-control"
              placeholder="(E-Mail)"
              required
            >
          </div>
        </div>
        <div class="col-3"></div>
      </div>

      <!--  -->
      <div class="row">
        <div class="col-3"></div>
        <div class="col-6">
          <div class="input-group">
            <div class="input-group-prepend">
              <label class="btn btn-outline-secondary" id="button-addon1" for="pwbox">
                <i class="fas fa-lock"></i>
              </label>
            </div>
            <input
              ref="password"
              id="pwbox"
              type="password"
              name="password"
              class="form-control"
              placeholder=""
              required
            >
          </div>
        </div>
        <div class="col-3"></div>
      </div>

      <!--   -->
      <div class="row">
        <div class="col-3"></div>
        <div class="col-6">
          <button type="submit" class="btn btn-color1 btn-block"></button>
        </div>
        <div class="col-3"></div>
      </div>

      <!--   -->
    </form>
    <div class="row">
      <div class="col-3"></div>
      <div class="col-6">
        <hr>
      </div>
      <div class="col-3"></div>
    </div>

    <!--   -->
    <div class="row">
      <div class="col-3"></div>
      <div class="col-6">
        <button @click="clickSignup" class="btn btn-color2 btn-block"></button>
      </div>
      <div class="col-3"></div>
    </div>

    <!--   -->
  </div>
</template>

<script>
import logo from "../etc/logo.vue";
export default {
  components: {
    logo: logo
  },
  methods: {
    doPost: function() {
      var self = this;
      // spring LoginController.java @PostMapping  email, password value .
      this.$http
        .post("/login", {
          email: self.$refs["email"].value,
          password: self.$refs["password"].value
        })
        .then(function(response) {
          sessionStorage.setItem("user", JSON.stringify(response.data));
          self.$router.push("/");
        })
        .catch(function(error) {
          alert(",    .");
          console.log(error);
        });
    },
    clickSignup: function() {
      this.$router.push("/signup");
    }
  }
};
</script>

<style>
.logo {
  margin: 10px;
  font-size: 2em;
  padding: 1.2em;
}

label i {
  width: 20px;
}

.form-control:focus {
  border-color: none;
  box-shadow: none;
  /* border-color: #FF0000; */
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

textarea:focus,
input:focus {
  outline: red;
}
</style>



