<template>
  <div v-if="select">
    <b-modal id="modal1" title="" hide-footer>
      <h3 class="text-left"></h3>
      <hr>
      <div class="row">
        <div class="col-4">
          <img :src="getImgUrl(product[0].id)" alt width="100" height="100">
        </div>
        <div class="col-8">{{productView}}</div>
      </div>
      <hr>
      <h3 class="text-left"> </h3>
      <table class="table" v-if="data">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{data.name}}</td>
            <td>{{data.phone}}</td>
            <td>{{data.email}}</td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left">  </h3>
      <table class="table" v-if="prop">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{prop.name}}</td>
            <td>{{prop.phone}}</td>
            <td>{{prop.address}}</td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left"></h3>
      <table class="table" v-if="info">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{info.price}} </td>
            <td>{{info.charge!='0'?info.charge+' ':''}}</td>
            <td>{{info.total}} </td>
          </tr>
        </tbody>
      </table>

      <h3 class="text-left"> : {{methodView}}</h3>
      <table class="table" v-if="select.radioSelected=='card'">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{cardView}}</td>
            <td>{{halbuView}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='mutong'">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{bankView}}</td>
            <td>{{select.date}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='account'">
        <thead>
          <tr>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{bankView}}</td>
          </tr>
        </tbody>
      </table>

      <table class="table" v-if="select.radioSelected=='phone'">
        <thead>
          <tr>
            <th> </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{select.phoneSelected | uppercase}}</td>
          </tr>
        </tbody>
      </table>
    </b-modal>
  </div>
</template>

<script>
export default {
  props: ["prop"],
  data() {
    return {
      info: null,
      data: null,
      select: null,
      product: null
    };
  },
  created() {
    this.product = JSON.parse(sessionStorage.getItem("order"));

    this.$bus.$on("method", data => {
      this.select = data;
    });
    this.data = JSON.parse(sessionStorage.getItem("user"));
    this.info = JSON.parse(sessionStorage.getItem("info"));
  },
  filters: {
    uppercase: function(v) {
      if (v) {
        return v.toUpperCase();
      }
    }
  },
  computed: {
    methodView: function() {
      let result = "";
      switch (this.select.radioSelected) {
        case "card":
          result = "";
          break;
        case "mutong":
          result = "";
          break;
        case "account":
          result = " ";
          break;
        case "phone":
          result = "";
          break;
        default:
          break;
      }
      return result;
    },
    cardView: function() {
      let result = "";
      switch (this.select.cardSelected) {
        case "shinhan":
          result = "";
          break;
        case "samsung":
          result = "";
          break;
        case "hyundai":
          result = "";
          break;
        case "lotte":
          result = "";
          break;
        default:
          break;
      }
      return result;
    },
    bankView: function() {
      let result = "";
      switch (this.select.bankSelected) {
        case "shinhan":
          result = "";
          break;
        case "kb":
          result = "";
          break;
        case "nh":
          result = "";
          break;
        case "woori":
          result = "";
          break;
        case "hana":
          result = "";
          break;
        case "ibk":
          result = "";
          break;
        default:
          break;
      }
      return result;
    },
    halbuView: function() {
      let result = "";
      switch (this.select.halbuSelected) {
        case "All":
          result = "";
          break;
        case 2:
          result = "2()";
          break;
        case 4:
          result = "4()";
          break;
        case 6:
          result = "6";
          break;
        case 8:
          result = "8";
          break;
        case 10:
          result = "10";
          break;
        case 12:
          result = "12";
          break;
        default:
          break;
      }
      return result;
    },
    productView: function() {
      if (this.product.length > 1) {
        return `${this.product[0].name} | ${this.product[0].size} | ${
          this.product[0].color
        }  ${this.product.length - 1}`;
      } else {
        return `${this.product[0].name} | ${this.product[0].size} | ${
          this.product[0].color
        }`;
      }
    }
  },
  methods: {
    getImgUrl: function(id) {
      var images = require.context("../../productimg", false, /\.jpg$/);
      return images("./" + id + ".jpg");
    }
  }
};
</script>

<style>
.modal-header {
  color: white;
  background-color: #bf1011;
}
</style>



