<template>
  <div>
    <h2></h2>
    <div id="radio">
      <b-form-radio-group v-model="radioSelected" name="method" class="radio">
        <div class="row">
          <div class="col-3">
            <b-form-radio value="card"></b-form-radio>
          </div>
          <div class="col-3">
            <b-form-radio value="mutong"></b-form-radio>
          </div>
          <div class="col-3">
            <b-form-radio value="account"> </b-form-radio>
          </div>
          <div class="col-3">
            <b-form-radio value="phone"></b-form-radio>
          </div>
        </div>
      </b-form-radio-group>
    </div>
    <table class="table" id="methodTable">
      <tbody v-if="radioSelected=='card'">
        <tr>
          <th>
            <label for="card"></label>
          </th>
          <td>
            <b-form-select id="card" v-model="cardSelected" :options="cardOptions" class="mb-3"/>
          </td>
        </tr>
        <tr>
          <th>
            <label for="halbu"></label>
          </th>
          <td>
            <b-form-select id="halbu" v-model="halbuSelected" :options="halbuOptions" class="mb-3"/>
          </td>
        </tr>
      </tbody>
      <tbody v-if="radioSelected=='mutong'">
        <tr>
          <th>
            <label for="bank"></label>
          </th>
          <td>
            <b-form-select id="bank" v-model="bankSelected" :options="bankOptions" class="mb-3"/>
          </td>
        </tr>
        <tr>
          <th>
            <label></label>
          </th>
          <td>
            <span>{{date}}</span>
          </td>
        </tr>
      </tbody>
      <tbody v-if="radioSelected=='account'">
        <tr>
          <th>
            <label></label>
          </th>
          <td>
            <b-form-select id="bank" v-model="bankSelected" :options="bankOptions" class="mb-3"/>
          </td>
        </tr>
      </tbody>
      <tbody v-if="radioSelected=='phone'">
        <tr>
          <th>
            <label> </label>
          </th>
          <td>
            <b-form-select id="phone" v-model="phoneSelected" :options="phoneOptions" class="mb-3"/>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      date: null,
      radioSelected: "card",
      cardSelected: null,
      halbuSelected: "All",
      bankSelected: null,
      phoneSelected: null,
      cardOptions: [
        { value: null, text: "" },
        { value: "shinhan", text: "" },
        { value: "samsung", text: "" },
        { value: "hyundai", text: "" },
        { value: "lotte", text: "" }
      ],
      halbuOptions: [
        { value: "All", text: "" },
        { value: 2, text: "2()" },
        { value: 4, text: "4()" },
        { value: 6, text: "6" },
        { value: 8, text: "8" },
        { value: 10, text: "10" },
        { value: 12, text: "12" }
      ],
      bankOptions: [
        { value: null, text: "" },
        { value: "shinhan", text: "" },
        { value: "kb", text: "" },
        { value: "nh", text: "" },
        { value: "woori", text: "" },
        { value: "hana", text: "" },
        { value: "ibk", text: "" }
      ],
      phoneOptions: [
        { value: null, text: "" },
        { value: "skt", text: "SKT" },
        { value: "kt", text: "KT" },
        { value: "lg", text: "LG" }
      ]
    };
  },
  created() {
    this.$bus.$emit("method", this.radioSelected);
    this.date = moment()
      .add(1, "days")
      .format("YYYY[] MM[] Do h[] mm[ ]");
  },
  watch: {
    radioSelected: function() {
      let select = {
        radioSelected: this.radioSelected,
        cardSelected: this.cardSelected,
        halbuSelected: this.halbuSelected,
        bankSelected: this.bankSelected,
        phoneSelected: this.phoneSelected
      };
      this.$bus.$emit("method", select);
    },
    cardSelected: function() {
      let select = {
        radioSelected: this.radioSelected,
        cardSelected: this.cardSelected,
        halbuSelected: this.halbuSelected,
        bankSelected: this.bankSelected,
        phoneSelected: this.phoneSelected
      };
      this.$bus.$emit("method", select);
    },
    halbuSelected: function() {
      let select = {
        radioSelected: this.radioSelected,
        cardSelected: this.cardSelected,
        halbuSelected: this.halbuSelected,
        bankSelected: this.bankSelected,
        phoneSelected: this.phoneSelected
      };
      this.$bus.$emit("method", select);
    },
    bankSelected: function() {
      let select = {
        radioSelected: this.radioSelected,
        cardSelected: this.cardSelected,
        halbuSelected: this.halbuSelected,
        bankSelected: this.bankSelected,
        phoneSelected: this.phoneSelected,
        date: this.date
      };
      this.$bus.$emit("method", select);
    },
    phoneSelected: function() {
      let select = {
        radioSelected: this.radioSelected,
        cardSelected: this.cardSelected,
        halbuSelected: this.halbuSelected,
        bankSelected: this.bankSelected,
        phoneSelected: this.phoneSelected
      };
      this.$bus.$emit("method", select);
    }
  }
};
</script>

<style>
#radio {
  margin: 30px;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background-color: #bf1011;
}

.custom-radio .custom-control-input:checked ~ .custom-control-label::before {
  background-color: #bf1011;
}
</style>



