<template>
  <div>
    <h2> </h2>
    <table class="table">
      <tbody>
        <tr>
          <th></th>
          <td>{{data.name}}</td>
        </tr>
        <tr>
          <th></th>
          <td>{{data.phone}}</td>
        </tr>
        <tr>
          <th></th>
          <td>{{data.email}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: null
    };
  },
  created() {
    this.data = JSON.parse(sessionStorage.getItem("user"));
  }
};
</script>

<style>
</style>



