<template>
  <div id="search">
    <!--
    @submit.prevent="getResult" : form   "getResult" 
    .prevent : ( )  .
    method="get" -> get :   
    -->
    <form @submit.prevent="getResult" method="get">
      <div class="input-group">
        <!-- ref = kwd : input    -->
        <input ref="kwd" type="text" name="kwd" class="form-control" id="ipt">
        <div class="input-group-append">
          <button class="btn" type="submit" id="btn_search">
            <i class="fas fa-search"></i>
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  methods: {
    getResult() {
      // this vue .  vue self .
      var self = this;
      // this.$http :  (index.js) axios 
      // axios :     
      this.$http
        //  input  URL 
        .get("/search?kwd=" + self.$refs["kwd"].value)
        .then(function(response) {
          // response     'result' 
          sessionStorage.setItem("result", JSON.stringify(response.data));
          //    vue    self 
          self.$router.push("/search?kwd=" + self.$refs["kwd"].value);
        })
        .catch(function(error) {
          console.log(error);
        });
      //  
      // var response = {
      //   data: {
      //     color: null,
      //     deliveryCharge: " ",
      //     detail: "",
      //     hitcount: 0,
      //     id: 1,
      //     image: "ho1.jpg",
      //     name: "",
      //     price: 100000,
      //     size: ""
      //   }
      // };
      // sessionStorage.setItem("result", JSON.stringify(response));
      // this.$router.push("/search");
    }
  }
};
</script>

<style scoped>

#btn_search {
  background-color: #ffffff;
  height: 3em;
  border-top: 3px solid #f20b04;
  border-right: 3px solid #f20b04;
  border-bottom: 3px solid #f20b04;
  border-left: 0px #ffffff;
  color: #f20b04;
  font-weight: bold;
}
#ipt{
  height: 3em;
  border-top: 3px solid #f20b04;
  border-left: 3px solid #f20b04;
  border-bottom: 3px solid #f20b04;
}
#search {
  margin-top: 50px;
  margin-left: 10px;
}

input:focus{
  border: 0px ;
  box-shadow: none;
}
button:focus{
  border: 0px ;
  box-shadow: none;
}
</style>



