<template lang="html">
  <div id="result">
     : {{result.length}} 
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: null
    };
  },
  /*
    params    $route  .
     watch: {
       '$route' (to, from) {
         //   ...
       }
     }
  */
  watch: {
    $route(to, from) {
      this.result = JSON.parse(sessionStorage.getItem("result"));
    }
  },
  created() {
    this.result = JSON.parse(sessionStorage.getItem("result"));
  }
};
</script>

<style>
#result {
  margin-top: 30px;
  margin-bottom: 50px;
  font-size: 20px;
}
</style>



