<template lang="html">
  <div class="search">
    <Header></Header>
    <Result></Result>
    <div v-for="(block,index) of result" :key="index" class="searchblock">
          <!-- Block components data block   .  ->  -->
          
           <Block v-bind:data="block" class="searchblock_c"></Block>
      </div>
    <div v-if="result.length==0">
       .
    </div>
</div>
</template>

<script>
import Header from "../main/Header/Header";
import Result from "./modules/Result";
import Block from "./modules/Block";
export default {
  components: {
    Result: Result,
    Block: Block,
    Header: Header
  },
  data() {
    return {
      result: null
    };
  },
  /*
  (    watch  )
    params    $route  .
     watch: {
       '$route' (to, from) {
         //   ...
       }
     }
  */
  watch: {
    $route(to, from) {
      this.result = JSON.parse(sessionStorage.getItem("result"));
    }
  },
  created() {
    this.result = JSON.parse(sessionStorage.getItem("result"));
  }
};
</script>

<style>
.search {
  font-size: 16px;
}
.searchblock {
  float: left;
}

.searchblock_c {
  cursor: pointer;
}

.searchblock_c:hover {
  box-shadow: 1px 1px 30px #bab8b8;
}
</style>



