<template>
  <div id="login">
      <div v-if="!user" class="login text-center">
        <router-link to="/login" class="text-color1"><i class="fas fa-air-freshener"></i>&nbsp;</router-link>
      </div>

      <div v-if="user" class="logout text-center  " >
        <span class="name">{{user.name}}</span>
        <br>
        <router-link to="/cart" class="txtCart">
          <i class="fas fa-shopping-cart"></i> 
        </router-link>
        <!-- sessionStorage user  ,  . -->
        <div class="btn" @click="logout"></div>
      </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: null
    };
  },
  methods: {
    logout() {
      sessionStorage.removeItem("user");
      location.reload();
    }
  },
  created() {
    // data methods       .
    // sessionStorage user  json  .
    this.user = JSON.parse(sessionStorage.getItem("user"));
  }
};
</script>

<style>
.name {
  color: #3d8e64;
  font-size: 1.5rem;
}
a:hover {
  cursor: pointer;
  text-decoration: none;
  color: #014014;
}



.login a {
  font-size: 1.5rem;
}
.logout .btn {
  color: black;
}


.logout a {
  color: #f20b04;
  font-size: 1.2rem;
}
.logout .btn:hover {
  cursor: pointer;
  color: black;
}

.login{
  /* height: 100px; */
  margin-top: 35px;
  padding: 20px;
  /* border: 1px solid black; */
}

.logout{
  /* height: 100px; */
  margin-top: 22px;
  padding: 20px;
  /* border: 1px solid black; */
}


</style>



